@import 'constants';
@import url('fonts.css');

html {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 18px;
}

body {
  position: relative;
  margin: 0 auto;
  padding: 0;
  width: 100%;
  background-color: $BACKGROUND-COLOR;

  font: normal $TEXT-SIZE-MOBILE $FONT-TEXT;
  color: $TEXT-COLOR;

  @include media-query(DESKTOP) {
    font: normal $TEXT-SIZE $FONT-TEXT;
  }
}

strong {
  font-weight: bold;
}

em {
  font-style: italic;
}

s {
  text-decoration:line-through;
}

a {
  font: normal $TEXT-SIZE $FONT-TEXT;
  color: $LINK-COLOR;
  cursor: pointer;
  text-decoration: none;
  transition: color $TRANSITION-PERIOD linear;

  &:visited {
    color: $LINK-COLOR-VISITED;
  }

  &:hover {
    color: $LINK-COLOR-HOVER;
  }
}

#menu-mobile {
  display: block;
  width: 100%;
  z-index: 10;
  position: fixed;
  top: 0;
  padding: 30px 0 0;
  text-align: center;
  background-color: $MENU-BACKGROUND-COLOR;

  .main-title {
    padding-bottom: 20px;
  }

  .language-menu {
    text-align: right;
    padding: 0 32px 10px;

    .language {
      margin: 0 8px;
    }
    .language-selected {
      color: $MENU-TEXT-COLOR;
      cursor: default;
      user-select: none;
      pointer-events: none;
      border-bottom: 3px solid $MENU-TEXT-COLOR;
    }
  }

  #menu-retratil-mobile {
    position: absolute;
    z-index: 20;
    width: 100%;
    background-color: $MENU-BACKGROUND-COLOR;
    top: 0;
    padding-top: 30px;
    transition: left 0.3s linear;
  }

  .show-menu {
    position: absolute;
    left: 2 * $RESPONSIVE-SPACING;
    top: 30px;
    font-size: 24px;
    width: 32px;
    img {
      width: 100%;
    }
  }

  .hide-menu {
    position: absolute;
    top: 20px;
    right: 2 * $RESPONSIVE-SPACING;
    font-size: 24px;
  }

  .menu-section {
    width: 66%;
    margin: 0 auto;
  }

  @include media-query(DESKTOP) {
    display: none;
  }
}

.menu-hide {
  left: -100vw;
  @include media-query(DESKTOP) {
    left: 0;
    top: -155px;
  }
}
.menu-show {
  left: 0;
  @include media-query(DESKTOP) {
    top: 0;
  }
}

.menu-section {
  border-top: 2px $MENU-TEXT-COLOR solid;
  padding: 10px 0;
  li a {
    text-transform: uppercase;
    font: normal $MENU-ITEM-SIZE $FONT-MENU;
    line-height: 1.7;
    vertical-align: middle;
    color: $MENU-TEXT-COLOR;
    &:hover {
      color: $MENU-TEXT-COLOR-HOVER;
    }
  }
}

.menu-logo {
  height: 100%;
  min-height: 90px;
  background-image: url("/3ed/media/images/logos/silo.svg");
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
  margin-bottom: 10px;
  @include media-query(DESKTOP) {
    margin-bottom: 0;
  }
}

#menu {
  position: fixed;
  z-index: 10;
  transition: top $TRANSITION-PERIOD linear;
  padding: 30px 0;
  box-sizing: border-box;
  background-color: $MENU-BACKGROUND-COLOR;
  width: 100%;
  margin: 0 auto;
  display: none;

  @include media-query(DESKTOP) {
    width: 100%;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
    padding: 30px 12% 0 8%;
  }

  .menu-title {
    width: 25%;
  }

  #menu-button {
    width: 100%;
    margin: 5px auto;
    text-align: center;
    font-size: 24px;
  }

  .menu-container {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    padding: 0 2vw;
  }

  .menu-section {
    width: 20%;
    max-width: 200px;
    min-width: 120px;
  }
  .menu-section+.menu-section {
    margin-left: 10%;
  }
}

#menu-mobile ul, #menu ul {
  padding: 0;
  margin: 0;
  list-style-type: none;
}

.menu-item {
  padding: 0;
  font-size: $MENU-ITEM-SIZE;
  font-weight: normal;
  color: $MENU-TEXT-COLOR;
  text-transform: none;
  display: inline;

  @include media-query(DESKTOP) {
    font-size: $MENU-ITEM-SIZE;
  }

  &:visited, a:visited {
    color: $MENU-TEXT-COLOR;
  }

  &:hover {
    color: $MENU-TEXT-COLOR-HOVER;
  }
}

.menu-icon {
  font: normal 1.5rem $FONT-MENU;
  cursor: pointer;
  transition: color $TRANSITION-PERIOD linear;

  color: $MENU-TEXT-COLOR;
  &:hover {
    color: $MENU-TEXT-COLOR-HOVER;
  }
}

.title-section.title-sticky {
  position: sticky;
  margin-top: -25px;
  top: 10px;
  z-index: 8;
}

.title-section {
  display: none;
  background-color: $SITE-ACCENT-COLOR;
  padding-bottom: 20px;

  @include media-query(DESKTOP) {
    display: block;
  }

  .title-image {
    width: 100%;
    padding-bottom: 10%;
    background-image: url(/3ed/media/images/covers/lab-cover.png);
    background-repeat: no-repeat;
    background-size: contain;
    background-position: left bottom;
  }

  .language-menu {
    box-sizing: border-box;
    max-width: 700px;
    text-align: right;
    margin: -32px auto 0;
    padding: 0 20px;

    .language {
      margin: 0 8px;
      transition: color $TRANSITION-PERIOD linear;
      color: $TEXT-COLOR;

      &:visited {
        color: $TEXT-COLOR;
      }

      &:hover {
        color: $MENU-TEXT-COLOR;
      }
    }

    .language.language-selected {
      color: $MENU-TEXT-COLOR;
      cursor: default;
      user-select: none;
      pointer-events: none;
      border-bottom: 3px solid $MENU-TEXT-COLOR;
    }
  }
}

.main-title {
  border-bottom: none;
  margin: 0 auto;
  width: 60%;
  text-align: center;
  transition: color $TRANSITION-PERIOD linear;
  color: $MENU-TEXT-COLOR;
  font-size: 1.1em;

  @include media-query(DESKTOP) {
    border-bottom: 3px $TEXT-COLOR solid;
    color: $LINK-COLOR;
    font-size: 1.5em;
    text-align: left;
    margin-top: 20px;
    width: 100%;
    &:hover {
      color: $LINK-COLOR-HOVER;
    }
  }
}

.breadcrumb-item {
  text-decoration: underline;
  font-weight: 900;
}
.breadcrumb-selected {
  text-decoration: none;
  color: $LINK-COLOR-HOVER;
  font-weight: 400;
}

.post-title, .post-date {
  font-family: $FONT-TITLE;
  font-weight: bold;
}

.post-title {
  line-height: 1;
  margin-bottom: 8px;
  text-align: left;
}

.post-date {
  font-weight: normal;
  font-size: $TEXT-SIZE-MOBILE;
  margin-top: 2px;
  line-height: 1;
  text-transform: lowercase;
}

.main-div-background.main-div-background-home {
  //background-image: url(/3ed/media/images/covers/apoio_00_abacate.png);
}

.main-div-background {
  display: none;
  @include media-query(DESKTOP) {
    display: block;
    position: fixed;
    z-index: -100;
    top: $MENU-HEIGHT;
    right: -30%;
    width: 100%;
    height: 100%;

    //background-image: url(/3ed/media/images/covers/apoio_00_verde.png);
    background-repeat: no-repeat;
    background-size: contain;
    background-position: right center;
  }
}

.main-div {
  display: block;
  width: 80%;
  box-sizing: border-box;
  margin: $MENU-MOBILE-HEIGHT auto $RESPONSIVE-SPACING;

  @include media-query(DESKTOP) {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 100%;
    min-height: 100vh;
    margin: $MENU-HEIGHT auto $RESPONSIVE-SPACING;
  }

  section + section {
    margin-top: 45px;
  }
  section.content-container {
    margin-top: 25px;
  }
  section.post-content-container {
    margin-top: 25px;
    @include media-query(DESKTOP) {
      margin-top: 45px;
    }
  }
  section.breadcrumbs-container {
    margin-top: -16px;
    @include media-query(DESKTOP) {
      margin-top: 8px;
    }
  }

  content {
    img {
      display: block;
      margin: 0 auto;
      max-width: 100%;
      border-radius: 5px;
    }
  }
}

.content-text {
  width: 100%;
  box-sizing: border-box;
  margin: 0 auto;
  padding: 0 20px;
  font: 200 $TEXT-SIZE $FONT-TEXT;
  line-height: 1.6;
  word-spacing: 0.1rem;
  text-align: left;

  @include media-query(DESKTOP) {
    max-width: 700px;
    text-align: justify;
  }

  a {
    text-decoration: underline;
    font-weight: bold;
  }
  img {
    display: block;
    width: 100%;
  }
  ul, ol {
    padding-inline-start: 16px;
  }
}

.project-list {
  border-top: 1px #000 dashed;
  padding-top: 26px;
}

.list-title {
  line-height: 1.3;
  font-family: "Font3", "Arial";
}

.list-wrapper {
  width: 100%;
  margin: 0 auto;
  @include media-query(DESKTOP) {
    width: 85%;
  }

  h2 {
    text-transform: capitalize;
  }
}

.more-news-link {
  margin: 0;
  text-align: right;
}

.post-list, .home-list {
  display: flex;
  flex-direction: row;
  width: 100%;

  .list-item {
    &:hover {
      .list-item-image.hover-contrast {
        transition: filter $TRANSITION-PERIOD linear;
        filter: grayscale(90%) contrast(120%) brightness(1.2) blur(8px);
      }
    }
  }

  .list-item-image {
    display: block;
    position: relative;
    width: 100%;
    height: 100%;
    border-radius: 5px;

    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    filter: grayscale(90%) contrast(120%) brightness(1.2) blur(0px);
    transition: filter $TRANSITION-PERIOD linear;
  }

  .tint-overlay {
    width: 100%;
    height: 100%;
    border-radius: 5px;
    position: absolute;
    top: 0;
    left: 0;
    background: linear-gradient($SITE-ACCENT-COLOR-GRADIENT-0, $SITE-ACCENT-COLOR-GRADIENT-1);
  }

  .hidden-link {
    opacity: 0;
    pointer-events: none;
    cursor: default;
  }

  .list-item-title {
    box-sizing: border-box;
    position: absolute;
    width: 100%;
    bottom: 0;
    padding: 8px;
    border-radius: 5px;
    font: normal $LINK-SIZE $FONT-TITLE;
    text-align: center;
    line-height: 1.2;
    color: #fff;
  }
}

.home-list {
  justify-content: center;
  flex-wrap: wrap;

  .list-item {
    display: block;
    position: relative;
    width: 90%;
    height: 150px;
    overflow: hidden;
    margin: 4px 1%;

    @include media-query(DESKTOP) {
      width: 23%;
      height: 100%;
      margin: 4px 1%;
    }
  }

  .list-item-image {
    width: 100%;
    height: 100%;
    padding-bottom: 100%;
  }
}

.post-list {
  justify-content: space-between;

  .list-item {
    display: block;
    position: relative;
    width: 80%;
    overflow: hidden;
    margin: 0 auto 24px;
    min-height: 200px;
    min-width: 260px;

    @include media-query(DESKTOP) {
      width: 45%;
      margin: 0 0 24px;
    }
  }
}

.people-list {
  margin: 0;
}

.project-person {
  list-style-type: none;
  color: $LINK-COLOR;
  font-size: 20px;
  font-family: "Font3";

  a {
    color: $LINK-COLOR;
    font-size: 20px;
    font-family: "Font3";
    text-decoration: underline;

    &:hover {
      color: $LINK-COLOR-HOVER;
    }
  }
}
.project-person + .project-person {
  margin-top: 5px;
}

.footer {
  margin: 0 auto;
  box-sizing: border-box;
  width: 80%;
  padding: 12px 0;
  border-top: 1px #000 dashed;
  @include media-query(DESKTOP) {
    width: 85%;
  }

  .menu-item {
    color: $LINK-COLOR;
  
    &:visited, a:visited {
      color: $LINK-COLOR;
    }

    &:hover {
      color: $LINK-COLOR-HOVER;
    }
  }
}

.footer:last-of-type {
  margin-bottom: 20px;
}

.footer-line {
  line-height: 1.7;
  vertical-align: middle;
}
a.footer-line {
  text-decoration: underline;
  font-weight: 200;
}

.footer-line-parcerias {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  a {
    margin: 4px;
    width: 23%;
    @include media-query(DESKTOP) {
      width: 11%;
    }
  }
}

i.fa-1_5x {
  font-size: 1.5em;
}

.parceira-logo {
  height: 64px;
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center center;
  filter: grayscale(100%);
  transition: filter $TRANSITION-PERIOD linear;

  &:hover {
    filter: grayscale(0%);
  }
}

.prev-next-container {
  width: 100%;
  margin: 0 auto;
  display: flex;
  flex-direction: row;
  justify-content: space-between;

  @include media-query(DESKTOP) {
    max-width: 700px;
  }

  .prev-next {
    text-transform: uppercase;
    padding: 12px;
    border-radius: 5px;
    border: 1px solid $TEXT-COLOR;
    background-color: $BACKGROUND-COLOR;
    text-align: center;
  }

  .prev-next-hide-0 {
    opacity: 0;
    user-select: none;
    pointer-events: none;
  }
}

.share-container {
  margin-top: 35px;
  h3.share-title {
    margin-bottom: 4px;
  }
  .share-icon-container {
    a {
      text-decoration: none;
    }
  }
}

.swipebox-gallery {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
  flex-direction: row;
  -webkit-flex-wrap: wrap;
  flex-wrap: wrap;
  -webkit-justify-content: center;
  justify-content: center;

  .swipebox {
    margin: 24px 5px 0;
    width: 45%;

    @include media-query(DESKTOP) {
      width: 48%;
    }

    img {
      width: 100%;
    }
  }
}

.video-wrapper-16x9 {
  padding-bottom: 56.25%;
}

.video-wrapper-4x3 {
  padding-bottom: 75%;
}

.video-wrapper {
  position: relative;
  margin: 1rem auto;
  height: 0;
  overflow: hidden;

  iframe {
    position: absolute;
    top:0;
    left: 0;
    width: 100%;
    height: 100%;
  }
}

.fof {
  font-size: 100px;
}
